import { useEffect, useRef } from "react";
import Typed from "typed.js";

export default function FirstView() {
  var el = useRef(null)
  useEffect(() => {
    var typped = new Typed(el.current, {
      strings: ['Web Developer', 'Django Developer', 'React Developer', 'Freelancer'],
      loop: true,
      startDelay: 500,
      typeSpeed: 100,
      backSpeed: 100,
      backDelay: 4000,
      smartBackspace: true,
      showCursor: true,
      cursorChar: "!"
    });
    return () => {
      typped.distroy();
    }
  }, [])

  return (
    // ======= Home Section =======
    <section id="home" className="d-flex flex-column justify-content-center">
      <div className="container" data-aos="zoom-in" data-aos-delay="100">
        <h1>Bilal Ahmed</h1>
        {/* <span ref={el}></span> */}
        <p>I'm <span ref={el}></span></p>
        {/* <p>I'm <span className="typed" id='typed' data-typed-items="Web Developer, Django Developer, React Developer, Freelancer"></span></p> */}
        <div className="social-links">
          <a href="https://twitter.com/bilalahmed_dev" target="_blank" rel="noreferrer" className="twitter"><i className="bx bxl-twitter"></i></a>
          <a href="https://www.facebook.com/bilalahmeddev" target="_blank" rel="noreferrer" className="facebook"><i className="bx bxl-facebook"></i></a>
          <a href="https://www.instagram.com/bilalahmed_dev/" target="_blank" rel="noreferrer" className="instagram"><i className="bx bxl-instagram"></i></a>
          <a href="skype:bilalahmedkhatri_1?chat" className="skype"><i className="bx bxl-skype"></i></a>
          <a href="https://www.linkedin.com/in/bilalahmeddev/" target="_blank" rel="noreferrer" className="linkedin"><i className="bx bxl-linkedin"></i></a>
        </div>
      </div>
    </section>
  )
}
