import { useState } from "react"


export function Header(props) {

    const [isActive, setIsActive] = useState('home')

    const menusIcons = [
        { menus: "home", icons : "home" },
        { menus: "about", icons : "user" },
        { menus: "resume", icons : "file-blank" },
        { menus: "testimonials", icons : "server" },
        { menus: "contact", icons : "envelope" }
    ]
    return (
        <>
            <header id="header" className="d-flex flex-column justify-content-center">
                <nav id="navlink" className="navbar nav-menu">
                    {menusIcons.map((x, key) => (
                        <li key={key}>
                            <a href={`#${x.menus}`} className={`nav-link scrollto ${isActive === x.menus && 'active'}`} onClick={() => setIsActive(x.menus)}>
                                <i className={`bx bx-${x.icons}`}></i>
                                <span>{x.menus.toUpperCase()}</span>
                            </a>
                        </li>
                    ))}
                </nav>
            </header>
        </>
    )
}
